import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from '@mui/material';
import { absenceService, AbsenceRequest } from '../../services/absenceService';

const AbsenceRequests: React.FC = () => {
  const [requests, setRequests] = useState<AbsenceRequest[]>([]);
  const [selectedRequest, setSelectedRequest] = useState<AbsenceRequest | null>(null);
  const [openDialog, setOpenDialog] = useState(false);
  const [comment, setComment] = useState('');
  const [filter, setFilter] = useState<'PENDING' | 'APPROVED' | 'REJECTED' | 'ALL'>('ALL');

  useEffect(() => {
    loadRequests();
  }, [filter]);

  const loadRequests = async () => {
    try {
      const data = await absenceService.getAbsenceRequests(
        filter === 'ALL' ? undefined : filter
      );
      setRequests(data);
    } catch (error) {
      console.error('Erreur lors du chargement des demandes:', error);
    }
  };

  const handleProcess = async (status: 'APPROVED' | 'REJECTED') => {
    if (!selectedRequest) return;

    try {
      await absenceService.processAbsenceRequest(
        selectedRequest.id,
        status,
        comment
      );
      setOpenDialog(false);
      setComment('');
      loadRequests();
    } catch (error) {
      console.error('Erreur lors du traitement de la demande:', error);
    }
  };

  const handleDownloadDocument = async (documentId: string) => {
    try {
      const blob = await absenceService.downloadDocument(documentId);
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `justificatif-${documentId}.pdf`;
      a.click();
    } catch (error) {
      console.error('Erreur lors du téléchargement:', error);
    }
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h5" gutterBottom>
        Gestion des demandes d'absence
      </Typography>

      <FormControl sx={{ mb: 3, minWidth: 200 }}>
        <InputLabel>Filtrer par statut</InputLabel>
        <Select
          value={filter}
          label="Filtrer par statut"
          onChange={(e) => setFilter(e.target.value as typeof filter)}
        >
          <MenuItem value="ALL">Tous</MenuItem>
          <MenuItem value="PENDING">En attente</MenuItem>
          <MenuItem value="APPROVED">Approuvé</MenuItem>
          <MenuItem value="REJECTED">Rejeté</MenuItem>
        </Select>
      </FormControl>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Étudiant</TableCell>
              <TableCell>Cours</TableCell>
              <TableCell>Date début</TableCell>
              <TableCell>Date fin</TableCell>
              <TableCell>Raison</TableCell>
              <TableCell>Statut</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {requests.map((request) => (
              <TableRow key={request.id}>
                <TableCell>{request.studentId}</TableCell>
                <TableCell>{request.courseId}</TableCell>
                <TableCell>{new Date(request.startDate).toLocaleDateString()}</TableCell>
                <TableCell>{new Date(request.endDate).toLocaleDateString()}</TableCell>
                <TableCell>{request.reason}</TableCell>
                <TableCell>{request.status}</TableCell>
                <TableCell>
                  {request.status === 'PENDING' && (
                    <Button
                      variant="contained"
                      onClick={() => {
                        setSelectedRequest(request);
                        setOpenDialog(true);
                      }}
                    >
                      Traiter
                    </Button>
                  )}
                  {request.document && (
                    <Button
                      variant="outlined"
                      onClick={() => handleDownloadDocument(request.document!)}
                      sx={{ ml: 1 }}
                    >
                      Justificatif
                    </Button>
                  )}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
        <DialogTitle>Traiter la demande d'absence</DialogTitle>
        <DialogContent>
          <TextField
            label="Commentaire"
            multiline
            rows={4}
            value={comment}
            onChange={(e) => setComment(e.target.value)}
            fullWidth
            sx={{ mt: 2 }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}>Annuler</Button>
          <Button
            onClick={() => handleProcess('REJECTED')}
            color="error"
            variant="contained"
          >
            Rejeter
          </Button>
          <Button
            onClick={() => handleProcess('APPROVED')}
            color="primary"
            variant="contained"
          >
            Approuver
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default AbsenceRequests;
