import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fr } from 'date-fns/locale';
import './App.css';
import Login from './pages/Login';
import ProfessorDashboard from './pages/professor/Dashboard';
import AttendanceManagement from './pages/professor/AttendanceManagement';
import QuizManagement from './pages/professor/QuizManagement';
import AbsenceRequests from './pages/professor/AbsenceRequests';
import SessionHistory from './pages/professor/SessionHistory';

function App() {
  return (
    <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fr}>
      <Router>
        <Routes>
          <Route path="/" element={<Login />} />
          <Route path="/professor/dashboard" element={<ProfessorDashboard />} />
          <Route path="/professor/attendance" element={<AttendanceManagement />} />
          <Route path="/professor/quiz" element={<QuizManagement />} />
          <Route path="/professor/absence-requests" element={<AbsenceRequests />} />
          <Route path="/professor/history" element={<SessionHistory />} />
        </Routes>
      </Router>
    </LocalizationProvider>
  );
}

export default App;
