import api from './api';

export interface Attendance {
  id: string;
  studentId: string;
  courseId: string;
  date: string;
  isPresent: boolean;
  type: 'COURSE' | 'TD' | 'TP' | 'EXAM';
}

export const attendanceService = {
  // Récupérer la liste des présences pour un cours
  getAttendanceList: async (courseId: string, date: string) => {
    const response = await api.get(`/attendance/${courseId}/${date}`);
    return response.data;
  },

  // Enregistrer les présences
  saveAttendance: async (attendanceData: Partial<Attendance>[]) => {
    const response = await api.post('/attendance', attendanceData);
    return response.data;
  },

  // Scanner un QR code
  scanQRCode: async (qrData: string) => {
    const response = await api.post('/attendance/scan', { qrData });
    return response.data;
  },

  // Récupérer les statistiques de présence
  getStatistics: async (courseId: string, startDate: string, endDate: string) => {
    const response = await api.get(`/attendance/statistics/${courseId}`, {
      params: { startDate, endDate }
    });
    return response.data;
  }
};
