import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  TextField,
  Button,
  Typography,
  Paper,
  InputAdornment,
  IconButton,
  useMediaQuery,
  ThemeProvider,
  createTheme,
} from '@mui/material';
import {
  Person,
  Lock,
  Visibility,
  VisibilityOff,
  LightMode,
  DarkMode,
} from '@mui/icons-material';
import FacebookIcon from '@mui/icons-material/Facebook';
import Link from '@mui/material/Link';
import '../assets/styles/Login.css';

const Login = () => {
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  const [darkMode, setDarkMode] = useState(() => {
    const savedTheme = localStorage.getItem('theme');
    return savedTheme ? savedTheme === 'dark' : prefersDarkMode;
  });

  const [showFooter, setShowFooter] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setShowFooter(window.scrollY > 100);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
    },
  });

  useEffect(() => {
    document.body.classList.toggle('dark-mode', darkMode);
  }, [darkMode]);

  const [credentials, setCredentials] = useState({
    identifier: '',
    password: '',
  });
  const [showPassword, setShowPassword] = useState(false);

  const toggleTheme = () => {
    const newTheme = !darkMode;
    setDarkMode(newTheme);
    localStorage.setItem('theme', newTheme ? 'dark' : 'light');
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setCredentials(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const [errors, setErrors] = useState({
    identifier: '',
    password: ''
  });

  const validateForm = () => {
    let isValid = true;
    const newErrors = { identifier: '', password: '' };

    if (!credentials.identifier) {
      newErrors.identifier = 'L\'identifiant est requis';
      isValid = false;
    } else if (credentials.identifier.length < 3) {
      newErrors.identifier = 'L\'identifiant doit contenir au moins 3 caractères';
      isValid = false;
    }

    if (!credentials.password) {
      newErrors.password = 'Le mot de passe est requis';
      isValid = false;
    } else if (credentials.password.length < 6) {
      newErrors.password = 'Le mot de passe doit contenir au moins 6 caractères';
      isValid = false;
    }

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (validateForm()) {
      // Votre logique de soumission
      console.log('Form submitted:', credentials);
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Box className={`login-container ${darkMode ? 'dark-mode' : ''}`}>
        <IconButton
          className="theme-toggle"
          onClick={toggleTheme}
          aria-label="toggle theme"
        >
          {darkMode ? <LightMode /> : <DarkMode />}
        </IconButton>

        <Container component="main" maxWidth="xs">
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'center',
              minHeight: '90vh',
            }}
          >
            <Paper elevation={0} className="login-card" sx={{ padding: 4, width: '100%' }}>
              <Box className="logo-container">
                <img 
                  src="/logo-fsei.png" 
                  alt="FSEI Mostaganem" 
                  className="logo-image"
                />
              </Box>

              <Typography 
                variant="h6" 
                align="center" 
                className="typing-text"
                sx={{ 
                  mb: 4,
                  display: 'inline-block',
                  width: 'fit-content'
                }}
              >
                Système de Gestion des Présences
              </Typography>
              
              <Box component="form" onSubmit={handleSubmit} sx={{ mt: 1 }}>
                <TextField
                  className="input-field"
                  required
                  fullWidth
                  id="identifier"
                  label="Identifiant"
                  name="identifier"
                  autoComplete="username"
                  autoFocus
                  variant="outlined"
                  value={credentials.identifier}
                  onChange={handleInputChange}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <Person sx={{ color: darkMode ? '#90caf9' : '#2196F3' }} />
                      </InputAdornment>
                    ),
                  }}
                  error={!!errors.identifier}
                  helperText={errors.identifier}
                />
                
                <TextField
                  className="input-field"
                  required
                  fullWidth
                  name="password"
                  label="Mot de passe"
                  type={showPassword ? 'text' : 'password'}
                  id="password"
                  variant="outlined"
                  autoComplete="current-password"
                  value={credentials.password}
                  onChange={handleInputChange}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <Lock sx={{ color: darkMode ? '#90caf9' : '#2196F3' }} />
                      </InputAdornment>
                    ),
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={togglePasswordVisibility}
                          edge="end"
                        >
                          {showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    ),
                  }}
                  error={!!errors.password}
                  helperText={errors.password}
                />

                <Button
                  className="submit-button"
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{ mt: 3, mb: 2 }}
                >
                  Se connecter
                </Button>
              </Box>
            </Paper>
          </Box>
        </Container>
        
        <Box className={`footer ${showFooter ? 'visible' : ''}`}>
          <Box className="footer-section">
            <Typography className="footer-title">INFORMATION</Typography>
            <Link href="https://www.univ-mosta.dz/" className="footer-link" target="_blank">
              www.univ-mosta.dz
            </Link>
            <Link href="https://www.univ-mosta.dz/fsei" className="footer-link" target="_blank">
              www.univ-mosta.dz/fsei
            </Link>
            <Link href="https://www.mesrs.dz/" className="footer-link" target="_blank">
              www.mesrs.dz
            </Link>
          </Box>

          <Box className="footer-section">
            <Typography className="footer-title">CONTACT</Typography>
            <Typography sx={{ color: darkMode ? '#fff' : '#666' }}>
              Chemin des cretes ex INES Mostaganem
            </Typography>
            <Typography sx={{ color: darkMode ? '#fff' : '#666' }}>
              Téléphone: +213 45 36 64 72
            </Typography>
            <Typography sx={{ color: darkMode ? '#fff' : '#666' }}>
              E-mail: admin.fseimoodle@univ-mosta.dz
            </Typography>
          </Box>

          <Box className="footer-section">
            <Typography className="footer-title">DÉVELOPPEUR</Typography>
            <Typography className="developer-name">
              Benderbouz Abdelkader Bilal
            </Typography>
          </Box>

          <Box className="footer-section">
            <Typography className="footer-title">SUIVEZ-NOUS</Typography>
            <Box className="social-icons">
              <Link href="https://www.facebook.com/fsei.mostaganem" target="_blank">
                <FacebookIcon />
              </Link>
            </Box>
          </Box>
        </Box>
      </Box>
    </ThemeProvider>
  );
};
export default Login;
