import React, { useState } from 'react';
import {
  Box,
  Typography,
  Button,
  Card,
  CardContent,
  Grid,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  List,
  ListItem,
  ListItemText,
  IconButton,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';

interface Quiz {
  id: string;
  title: string;
  type: 'quiz' | 'survey';
  questions: Question[];
}

interface Question {
  id: string;
  text: string;
  type: 'multiple' | 'single' | 'text';
  options?: string[];
}

const QuizManagement: React.FC = () => {
  const [quizzes, setQuizzes] = useState<Quiz[]>([]);
  const [openDialog, setOpenDialog] = useState(false);
  const [newQuiz, setNewQuiz] = useState<Partial<Quiz>>({
    title: '',
    type: 'quiz',
    questions: [],
  });

  const handleCreateQuiz = () => {
    if (newQuiz.title && newQuiz.type) {
      const quiz: Quiz = {
        id: Date.now().toString(),
        title: newQuiz.title,
        type: newQuiz.type as 'quiz' | 'survey',
        questions: newQuiz.questions || [],
      };
      setQuizzes([...quizzes, quiz]);
      setOpenDialog(false);
      setNewQuiz({ title: '', type: 'quiz', questions: [] });
    }
  };

  const handleDeleteQuiz = (quizId: string) => {
    setQuizzes(quizzes.filter(quiz => quiz.id !== quizId));
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h5" gutterBottom>
        Gestion des Quiz et Sondages
      </Typography>

      <Button
        variant="contained"
        onClick={() => setOpenDialog(true)}
        sx={{ mb: 3 }}
      >
        Créer un nouveau quiz/sondage
      </Button>

      <Grid container spacing={3}>
        {quizzes.map((quiz) => (
          <Grid item xs={12} sm={6} md={4} key={quiz.id}>
            <Card>
              <CardContent>
                <Typography variant="h6">{quiz.title}</Typography>
                <Typography color="textSecondary">
                  Type: {quiz.type === 'quiz' ? 'Quiz' : 'Sondage'}
                </Typography>
                <Typography>
                  {quiz.questions.length} question(s)
                </Typography>
                <Box sx={{ mt: 2 }}>
                  <IconButton onClick={() => handleDeleteQuiz(quiz.id)}>
                    <DeleteIcon />
                  </IconButton>
                  <IconButton>
                    <EditIcon />
                  </IconButton>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
        <DialogTitle>Créer un nouveau quiz/sondage</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 2 }}>
            <TextField
              label="Titre"
              value={newQuiz.title}
              onChange={(e) => setNewQuiz({ ...newQuiz, title: e.target.value })}
              fullWidth
            />
            <FormControl fullWidth>
              <InputLabel>Type</InputLabel>
              <Select
                value={newQuiz.type}
                label="Type"
                onChange={(e) => setNewQuiz({ ...newQuiz, type: e.target.value as 'quiz' | 'survey' })}
              >
                <MenuItem value="quiz">Quiz</MenuItem>
                <MenuItem value="survey">Sondage</MenuItem>
              </Select>
            </FormControl>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}>Annuler</Button>
          <Button onClick={handleCreateQuiz} variant="contained">
            Créer
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default QuizManagement;
