import api from './api';

export interface AbsenceRequest {
  id: string;
  studentId: string;
  courseId: string;
  startDate: string;
  endDate: string;
  reason: string;
  status: 'PENDING' | 'APPROVED' | 'REJECTED';
  document?: string;
}

export const absenceService = {
  // Récupérer les demandes d'absence
  getAbsenceRequests: async (status?: 'PENDING' | 'APPROVED' | 'REJECTED') => {
    const response = await api.get('/absence-requests', {
      params: { status }
    });
    return response.data;
  },

  // Créer une demande d'absence (pour le professeur)
  createAbsenceRequest: async (requestData: Partial<AbsenceRequest>) => {
    const response = await api.post('/absence-requests', requestData);
    return response.data;
  },

  // Traiter une demande d'absence
  processAbsenceRequest: async (
    requestId: string,
    status: 'APPROVED' | 'REJECTED',
    comment?: string
  ) => {
    const response = await api.put(`/absence-requests/${requestId}`, {
      status,
      comment
    });
    return response.data;
  },

  // Télécharger un justificatif
  downloadDocument: async (documentId: string) => {
    const response = await api.get(`/documents/${documentId}`, {
      responseType: 'blob'
    });
    return response.data;
  }
};
