import React, { useState, useEffect } from 'react';
import { TablePagination } from '@mui/material';
import {
  Box,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { attendanceService } from '../../services/attendanceService';

interface SessionStats {
  totalStudents: number;
  presentCount: number;
  absentCount: number;
  date: string;
  type: string;
}

const SessionHistory: React.FC = () => {
  const [sessions, setSessions] = useState<SessionStats[]>([]);
  const [selectedCourse, setSelectedCourse] = useState('');
  const [startDate, setStartDate] = useState<Date | null>(null);
  const [endDate, setEndDate] = useState<Date | null>(null);
  const [detailsOpen, setDetailsOpen] = useState(false);
  const [selectedSession, setSelectedSession] = useState<SessionStats | null>(null);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  // Cette fonction serait remplacée par un appel API réel
  const loadSessions = async () => {
    if (!selectedCourse || !startDate || !endDate) return;

    try {
      const stats = await attendanceService.getStatistics(
        selectedCourse,
        startDate.toISOString(),
        endDate.toISOString()
      );
      setSessions(stats);
    } catch (error) {
      console.error('Erreur lors du chargement des sessions:', error);
    }
  };

  useEffect(() => {
    if (selectedCourse && startDate && endDate) {
      loadSessions();
    }
  }, [selectedCourse, startDate, endDate]);

  const calculateAttendanceRate = (session: SessionStats) => {
    return ((session.presentCount / session.totalStudents) * 100).toFixed(1);
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h5" gutterBottom>
        Historique des séances
      </Typography>

      <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
        <FormControl sx={{ minWidth: 200 }}>
          <InputLabel>Cours</InputLabel>
          <Select
            value={selectedCourse}
            label="Cours"
            onChange={(e) => setSelectedCourse(e.target.value)}
          >
            <MenuItem value="COURSE1">Cours 1</MenuItem>
            <MenuItem value="COURSE2">Cours 2</MenuItem>
            <MenuItem value="COURSE3">Cours 3</MenuItem>
          </Select>
        </FormControl>

        <DatePicker
          label="Date début"
          value={startDate}
          onChange={(newValue) => setStartDate(newValue)}
        />

        <DatePicker
          label="Date fin"
          value={endDate}
          onChange={(newValue) => setEndDate(newValue)}
        />
      </Box>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Date</TableCell>
              <TableCell>Type</TableCell>
              <TableCell>Total Étudiants</TableCell>
              <TableCell>Présents</TableCell>
              <TableCell>Absents</TableCell>
              <TableCell>Taux de présence</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {sessions.map((session, index) => (
              <TableRow key={index}>
                <TableCell>{new Date(session.date).toLocaleDateString()}</TableCell>
                <TableCell>{session.type}</TableCell>
                <TableCell>{session.totalStudents}</TableCell>
                <TableCell>{session.presentCount}</TableCell>
                <TableCell>{session.absentCount}</TableCell>
                <TableCell>{calculateAttendanceRate(session)}%</TableCell>
                <TableCell>
                  <Button
                    variant="outlined"
                    onClick={() => {
                      setSelectedSession(session);
                      setDetailsOpen(true);
                    }}
                  >
                    Détails
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Dialog open={detailsOpen} onClose={() => setDetailsOpen(false)}>
        <DialogTitle>Détails de la séance</DialogTitle>
        <DialogContent>
          {selectedSession && (
            <Box sx={{ p: 2 }}>
              <Typography>
                Date: {new Date(selectedSession.date).toLocaleDateString()}
              </Typography>
              <Typography>Type: {selectedSession.type}</Typography>
              <Typography>
                Taux de présence: {calculateAttendanceRate(selectedSession)}%
              </Typography>
              {/* Ici, on pourrait ajouter plus de détails comme la liste des étudiants */}
            </Box>
          )}
        </DialogContent>
      </Dialog>
      // Dans le return, après le TableContainer
<TablePagination
  component="div"
  count={sessions.length}
  page={page}
  onPageChange={(e, newPage) => setPage(newPage)}
  rowsPerPage={rowsPerPage}
  onRowsPerPageChange={(e) => {
    setRowsPerPage(parseInt(e.target.value, 10));
    setPage(0);
  }}
  labelRowsPerPage="Lignes par page"
/>
    </Box>
  );
};
export default SessionHistory;
