import React, { useState } from 'react';
import {
  Box,
  Paper,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from '@mui/material';
import QrScanner from 'react-qr-scanner';

interface Student {
  id: string;
  name: string;
  present: boolean;
}
const AttendanceManagement: React.FC = () => {
  const [scanError, setScanError] = useState<string | null>(null);
  const [selectedClass, setSelectedClass] = useState('');
  const [selectedType, setSelectedType] = useState('');
  const [students, setStudents] = useState<Student[]>([]);
  const [isScanning, setIsScanning] = useState(false);

  // Mock data - replace with actual API calls
  const classes = ['Class A', 'Class B', 'Class C'];
  const sessionTypes = ['Cours', 'TD', 'TP', 'Examen'];

  const handleClassChange = (event: any) => {
    setSelectedClass(event.target.value);
    // TODO: Fetch students for selected class
    setStudents([
      { id: '1', name: 'Student 1', present: false },
      { id: '2', name: 'Student 2', present: false },
    ]);
  };

  const togglePresence = (studentId: string) => {
    setStudents(students.map(student =>
      student.id === studentId
        ? { ...student, present: !student.present }
        : student
    ));
  };

  const handleScan = (data: any) => {
    setScanError(null);
    if (data) {
      try {
        const scannedData = JSON.parse(data);
        if (scannedData.studentId) {
          togglePresence(scannedData.studentId);
          setIsScanning(false);
        }
      } catch (err) {
        setScanError('Format QR code invalide');
      }
    }
  };
  const handleError = (err: any) => {
    setScanError(err?.message || 'Erreur de scan');
    console.error('Erreur QR Scanner:', err);
  };

  const saveAttendance = () => {
    // TODO: Save attendance data to backend
    console.log('Saving attendance:', {
      class: selectedClass,
      type: selectedType,
      students: students,
    });
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h5" gutterBottom>
        Gestion des présences
      </Typography>

      <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
        <FormControl sx={{ minWidth: 200 }}>
          <InputLabel>Classe</InputLabel>
          <Select
            value={selectedClass}
            label="Classe"
            onChange={handleClassChange}
          >
            {classes.map((cls) => (
              <MenuItem key={cls} value={cls}>{cls}</MenuItem>
            ))}
          </Select>
        </FormControl>

        <FormControl sx={{ minWidth: 200 }}>
          <InputLabel>Type de séance</InputLabel>
          <Select
            value={selectedType}
            label="Type de séance"
            onChange={(e) => setSelectedType(e.target.value)}
          >
            {sessionTypes.map((type) => (
              <MenuItem key={type} value={type}>{type}</MenuItem>
            ))}
          </Select>
        </FormControl>

        <Button
          variant="contained"
          onClick={() => setIsScanning(true)}
        >
          Scanner QR Code
        </Button>
      </Box>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell>Nom</TableCell>
              <TableCell>Présence</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {students.map((student) => (
              <TableRow key={student.id}>
                <TableCell>{student.id}</TableCell>
                <TableCell>{student.name}</TableCell>
                <TableCell>{student.present ? 'Présent' : 'Absent'}</TableCell>
                <TableCell>
                  <Button
                    variant="outlined"
                    onClick={() => togglePresence(student.id)}
                  >
                    {student.present ? 'Marquer absent' : 'Marquer présent'}
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Box sx={{ mt: 2 }}>
        <Button
          variant="contained"
          onClick={saveAttendance}
          disabled={!selectedClass || !selectedType}
        >
          Enregistrer
        </Button>
      </Box>

      <Dialog open={isScanning} onClose={() => setIsScanning(false)}>
        <DialogTitle>Scanner le QR Code</DialogTitle>
        <DialogContent>
  {scanError && (
    <Typography color="error" sx={{ mb: 2 }}>
      {scanError}
    </Typography>
  )}
  {isScanning && (
    <QrScanner
      onError={handleError}
      onScan={handleScan}
      style={{ width: '100%' }}
    />
  )}
</DialogContent>
        <DialogActions>
          <Button onClick={() => setIsScanning(false)}>Fermer</Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default AttendanceManagement;